.wrapper {
  padding: 1rem;
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1rem;
}

h1 {
  margin: 0;
}

.content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: stretch;
  gap: 1rem;
  max-width: 100%;
}

.form {
  display: flex;
  align-items: stretch;
  flex-wrap: wrap;
  gap: 1rem;
  width: 100%;
  max-width: 100%;
}
